import React, {useState, useEffect} from 'react';
import '../styles/AdminBookingDetails.css';

const sequenceOptions = [
    {value: "1", label: "1 - 1/25() 12:30"},
    {value: "2", label: "2 - 1/25() 15:30"},
    {value: "3", label: "3 - 1/25() 18:30"},
    {value: "4", label: "4 - 1/26() 12:00"},
    {value: "5", label: "5 - 1/26() 15:00"},
    {value: "6", label: "6 - 1/26() 18:00"},
];


function AdminBookingDetails({selectedBooking, onModalClose, onDelete, onSave}) {
    const [isEditMode, setIsEditMode] = useState(false);
    const [editingData, setEditingData] = useState(null);

    useEffect(() => {
        setIsEditMode(false);
        setEditingData(selectedBooking ? {...selectedBooking} : null);
    }, [selectedBooking]);

    if (!selectedBooking || !editingData) {
        return null;
    }

    //  
    const handleEditClick = () => {
        setIsEditMode(true);
    };

    const handleChange = (field, value) => {
        setEditingData((prev) => ({
            ...prev,
            [field]: value,
        }));
    };

    //   ->   
    const handleCancelClick = () => {
        setIsEditMode(false);
        setEditingData({...selectedBooking});
    };

    //   ->    ,   
    const handleSaveClick = () => {
        if (!window.confirm('  ?')) {
            return
        }
        onSave(editingData);
        setIsEditMode(false);
    };

    const handleDeleteClick = () => {
        if (!window.confirm('  ?')) {
            return;
        }
        onDelete(editingData);
        setIsEditMode(false);
    };

    return (
        <div className="abp-detail-container">
            {/*   */}
            <div className="abp-header">
                <h3>  </h3>
                <button className="abp-close-btn" onClick={onModalClose}>
                    
                </button>
            </div>

            {/*  */}
            <div className="abp-detail-row">
                <label>:</label>
                <input
                    type="text"
                    value={editingData.bookName || ''}
                    // onChange={(e) => handleChange('bookName', e.target.value)}
                    disabled={true}
                    className={'read-only'}
                    // disabled={!isEditMode}
                    // className={!isEditMode ? 'read-only' : ''}
                />
            </div>

            {/*  */}
            <div className="abp-detail-row">
                <label>:</label>
                <input
                    type="number"
                    value={editingData.bookQuantity || ''}
                    onChange={(e) => handleChange('bookQuantity', e.target.value)}
                    disabled={!isEditMode}
                    className={!isEditMode ? 'read-only' : ''}
                />
            </div>

            {/*  */}
            <div className="abp-detail-row">
                <label>:</label>
                <select
                    value={editingData.showSequence || ''}
                    onChange={(e) => handleChange('showSequence', e.target.value)}
                    disabled={!isEditMode}
                    className={!isEditMode ? 'read-only' : ''}
                >
                    {sequenceOptions.map(option => (
                        <option key={option.value} value={option.value}>
                            {option.label}
                        </option>
                    ))}
                </select>
            </div>

            {/*  */}
            <div className="abp-detail-row">
                <label>:</label>
                <input
                    type="text"
                    value={editingData.phoneNumber || ''}
                    // onChange={(e) => handleChange('phoneNumber', e.target.value)}
                    disabled={true}
                    className={'read-only'}
                    // disabled={!isEditMode}
                    // className={!isEditMode ? 'read-only' : ''}
                />
            </div>

            {/*  */}
            <div className="abp-detail-row">
                <label>:</label>
                <input
                    type="text"
                    value={editingData.inviters?.join(', ')}
                    // onChange={(e) => setInvitersValue(e.target.value)}
                    disabled={true}
                    className={'read-only'}
                    // disabled={!isEditMode}
                    // className={!isEditMode ? 'read-only' : ''}
                />
            </div>

            {/*  */}
            <div className="abp-detail-row">
                <label>:</label>
                <input
                    type="text"
                    value={editingData.cheeringMessage || ''}
                    // onChange={(e) => handleChange('cheeringMessage', e.target.value)}
                    disabled={true}
                    className={'read-only'}
                    // disabled={!isEditMode}
                    // className={!isEditMode ? 'read-only' : ''}
                />
            </div>

            {/*   */}
            <div style={{display: 'flex', marginTop: 12}}>
                {isEditMode ? (
                    <>
                        <button
                            className="abp-cancel-booking-btn" onClick={handleDeleteClick}>
                            
                        </button>
                        <button className="abp-cancel-btn" onClick={handleCancelClick}>
                            
                        </button>
                        <button className="abp-save-btn" onClick={handleSaveClick}>
                            
                        </button>
                    </>
                ) : (
                    <button className="abp-edit-btn" onClick={handleEditClick}>
                        
                    </button>
                )}
            </div>
        </div>
    );
}

export default AdminBookingDetails;
